import { Component } from '@angular/core';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import { Insomnia } from '@ionic-native/insomnia/ngx';
import Clappr from 'clappr';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})

export class HomePage {

  player: any;
  images: object = {
    data: []
  };
  loggedIn = false;
  liveHls = 'https://streamspace.live/hls/tempoafrictv/livestream.m3u8';

  constructor(
    public screenOrientation: ScreenOrientation,
    private splashScreen: SplashScreen,
    private insomnia: Insomnia
  ) {
  }

  ngOnInit() {
    this.splashScreen.hide();
    this.resize();
    this.initClapprPlayer();
  }

  getAspectHeight() {
    const width = document.body.offsetWidth;
    return width * (9 / 16);
  }

  resize() {
    document.getElementById('player_container').style.height = this.getAspectHeight() + 'px';
  }

  initClapprPlayer() {
    this.player = new Clappr.Player({
      source: this.liveHls,
      autoPlay: true,
      mute: false,
      aspectRatio: 16 / 9,
      width: '100%',
      height: this.getAspectHeight(),
    });
    this.player.attachTo(document.getElementById('clapper_player')).play();
    this.bindEvents();
  }

  bindEvents() {
    this.player.on(Clappr.Events.PLAYER_FULLSCREEN, (state) => {
      this.onFullscreenChange(state);
    });
  }

  onFullscreenChange(state) {
    if (state) {
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.LANDSCAPE);
      this.insomnia.keepAwake();
    } else {
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
      this.insomnia.allowSleepAgain();
    }
  }
}